// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  DropdownButton,
  MenuItem,
  Modal,
} from 'react-bootstrap';

// custom
import NamekoEditor from './NamekoEditor';
import Nameko from './Nameko';

export const Shadow = ({ idx }) => (<div
  className='shadow'
>
  <img src={`./imgs/shadow_${idx}.png`} alt='deco' />
  <img src='./imgs/wood.png' alt='deco' />
</div>);

export default class extends Component {
  showEditor(nameko) {
    this.props.updateNamekoModal(nameko);
  }

  render() {
    const {
      show, showModal, selectedLevel, levels, selectedNamekos,
      onClickLevel, onClickAction, onSelectNameko, onSelectNamekos,
      namekoModal,
    } = this.props;
    const { mode, nameko } = namekoModal;

    const dropdownItems = levels.map((level, idx) => <MenuItem
      key={idx}
      eventKey={idx}
      active={level === selectedLevel}
      onClick={() => onClickLevel(level)}
    >
      {level.name}
    </MenuItem>);

    dropdownItems.splice(1, 0, <MenuItem key='divider' divider />);

    return (
      <Modal
        className='nameko-modal'
        show={show}
        onHide={() => {
          showModal(false);
          this.showEditor(false);
        }}
      >
        <Modal.Header>
          <div className='header-inner'>
            <Shadow idx={1} />
            <div className='text'>
              {mode === 'editor' ?
                <span>編輯菇菇技能與數值</span> :
                <span>選擇菇菇以加入估算組隊清單<br />可選擇預設帶入級數或自訂</span>
              }
            </div>
          </div>
        </Modal.Header>
        <Modal.Body
          style={{ backgroundImage: `url('./imgs/bg_modal.jpg')` }}
          onWheel={e => mode === 'editor' && e.nativeEvent.preventDefault()}
        >
          <div className='content' style={{ opacity: mode === 'editor' ? 0 : 100 }}>
            <div className='buttons'>
              <Button onClick={() => onSelectNamekos(selectedLevel.namekos)} style={{ marginRight: 5 }}>全選</Button>
              <Button onClick={() => onSelectNamekos([])}>全不選</Button>
              <div style={{ flex: 1}} />
              <DropdownButton id='nameko-modal-dropdown' title={selectedLevel.name}>
                {dropdownItems}
              </DropdownButton>
            </div>
            <div className='all-block'>
              {(() => {
                const comps = [];

                selectedLevel.namekos.forEach((nameko, idx) => {
                  comps.push(<Nameko
                    key={idx}
                    nameko={nameko}
                    isSelected={selectedNamekos.includes(nameko)}
                    onClick={() => onSelectNameko(nameko)}
                    onClickEdit={() => this.showEditor(nameko)}
                  />);

                  if (idx % 3 === 2 || idx === selectedLevel.namekos.length - 1) {
                    comps.push(<Clearfix key={`_${idx}_xs`} visibleXsBlock />);
                  }
                  if (idx % 4 === 3 || idx === selectedLevel.namekos.length - 1) {
                    comps.push(<Clearfix key={`_${idx}_sm`} visibleSmBlock />);
                  }
                  if (idx % 6 === 5 || idx === selectedLevel.namekos.length - 1) {
                    comps.push(<Clearfix key={`_${idx}_md`} visibleMdBlock />);
                  }
                });

                return comps;
              })()}
            </div>
          </div>
        </Modal.Body>
        <NamekoEditor
          nameko={nameko}
          show={mode === 'editor'}
          onClick={onClickAction}
        />
        <Modal.Footer>
          <Button
            bsStyle='primary'
            onClick={() => mode === 'list' ? showModal(false) : this.showEditor(false)}
          >
            完成
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
