// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Glyphicon,
} from 'react-bootstrap';

// custom
import {
  namekoSkills,
} from '../lib/data';
import {
  Skill,
  Value,
  SimpleNameko,
} from './Nameko';

class SkillArea extends Component {
  render() {
    const { name, skills, onClick } = this.props;

    return (
      <div className='skill-area'>
        {namekoSkills[name].map((skill, idx) => (
          <Button
            key={idx}
            onClick={() => onClick('skill', skill)}
          >
            <Skill
              skill={skill}
              isSelected={skills.indexOf(skill) !== -1}
            />
          </Button>
        ))}
      </div>
    );
  }
}

class ValueArea extends Component {
  render() {
    const { values, onClick } = this.props;

    return (
      <div className='value-area'>
        {values.map((value, idx) => (
          <div
            key={idx}
          >
            <Button
              bsSize='large'
              onClick={() => onClick('add_value', idx)}
            >
              <Glyphicon glyph='triangle-top' />
            </Button>
            <Value type={['red', 'blue', 'green'][idx]} value={value} />
            <Button
              bsSize='large'
              onClick={() => onClick('sub_value', idx)}
            >
              <Glyphicon glyph='triangle-bottom' />
            </Button>
          </div>
        ))}
      </div>
    );
  }
}

export default class NamekoEditor extends Component {
  render() {
    const { nameko, show, onClick } = this.props;

    if (!nameko) {
      return null;
    }

    const { name, skills, values } = nameko;

    return (
      <div
        className='editor'
        style={{ display: show ? 'flex' : 'none' }}
      >
        <SimpleNameko name={name} />
        <SkillArea
          name={name}
          skills={skills}
          onClick={(type, value) => onClick(nameko, type, value)}
        />
        <ValueArea
          values={values}
          onClick={(type, value) => onClick(nameko, type, value)}
        />
        <span className='notice'>注意：更動數值後就會覆蓋自訂清單</span>
      </div>
    );
  }
}
