// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  Col,
  Glyphicon,
  Label,
  Modal,
  Panel,
} from 'react-bootstrap';

// custom
import {
  allTunnels,
  tunnelBgs,
} from '../lib/data';
import {
  Skill,
  Value,
} from './Nameko';
import {
  Reward,
} from './ResultPanel';

class Memo extends Component {
  render() {
    const { memo } = this.props;
    const { type, valueThresholds: v, skillValueBonus: vb, skills: s, name, reward: r } = memo;

    return (
      <Col className='memo-wrapper' xs={12} sm={6}>
        <div className='memo'>
          <div>{name}</div>
          <div>
            <span>{type}</span>
            {v && v.map((v, idx) => <Value key={idx} type={v[0]} value={v[1]} />)}
            {vb && <span>
              , <Skill skill={vb[0]} />的<Value type={vb[1]}  value={vb[2]} showPlus />
            </span>}
            {s && s.map((s, idx) => <Skill key={idx} skill={s} />)}
          </div>
          <div>
            <Reward type={memo.rewardType} name={r[0]} />
            {`* ${r[1]}`}
          </div>
        </div>
      </Col>
    );
  }
}

export class Tunnel extends Component {
  constructor(props) {
    super(props);
    this.state = { expanded: false };
  }

  render() {
    const { expanded } = this.state;
    const { tunnel, isSelected, onClick } = this.props;
    const { name, /*count, */memos } = tunnel;

    return (
      <Panel
        className='tunnel'
        expanded={expanded}
        onToggle={() => null}
      >
        <Panel.Heading
          onClick={() => this.setState({ expanded: !expanded })}
        >
          <Label
            className='check-block'
            onClick={e => {
              e.stopPropagation();
              !isSelected && onClick(tunnel);
            }}
          >
            {isSelected ? <img className='check' src='./imgs/g_check.png' alt='check' /> : ' '}
          </Label>
          <Label>{name}</Label>
          {name === '中途' && <img className='omamori' src='./imgs/omamori1.png' alt='omamori1' />}
          {name === '深處' && <img className='omamori' src='./imgs/omamori2.png' alt='omamori2' />}
          <div className='spacer' />
          <Button active={expanded} bsSize='small'>
            <Glyphicon glyph='list' />
          </Button>
        </Panel.Heading>
        <Panel.Collapse>
          <Panel.Body>
            {/*<div>{'菇菇數量: ' + (count || 3)}</div>*/}
            <div>
              {(() => {
                const comps = [];

                memos.forEach((memo, idx) => {
                  comps.push(<Memo key={idx} memo={memo} />);

                  if (idx % 2 === 1 || idx === memos.length - 1) {
                    comps.push(<Clearfix key={`_${idx}`} visibleSmBlock />);
                  }
                });

                return comps;
              })()}
            </div>
          </Panel.Body>
        </Panel.Collapse>
      </Panel>
    );
  }
}

export class TunnelBanner extends Component {
  constructor(props) {
    super(props);
    this.state = { expanded: !!props.isSelected };
  }

  render() {
    const { expanded } = this.state;
    const { children, text, bgKey, isSelected } = this.props;
    //console.log('banner props', this.props);

    const selectable = typeof isSelected === 'boolean';
    const className = 'tunnel-banner' + (selectable ? ' selectable' : '');

    return (
      <Panel
        className={className}
        expanded={expanded}
        onToggle={() => null}
      >
        <Panel.Heading
          style={{ backgroundImage: `url(${tunnelBgs[bgKey]})` }}
          onClick={() => selectable && this.setState({ expanded: !expanded })}
        >
          {selectable && <Label className='check-block'>
            {isSelected ? <img className='check' src='./imgs/g_check.png' alt='check' /> : ' '}
          </Label>}
          <Label>{text}</Label>
          <div className='spacer' />
          {children && <Button onClick={() => this.setState({ expanded: !expanded })} bsSize='small'>
            <Glyphicon glyph={`triangle-${expanded ? 'top' : 'bottom'}`} />
          </Button>}
        </Panel.Heading>
        {children && <Panel.Collapse>
          <Panel.Body>
            {children}
          </Panel.Body>
        </Panel.Collapse>}
      </Panel>
    );
  }
}

class TunnelGroup extends Component {
  render() {
    const { group, clickedTunnel, onClick } = this.props;
    const { groupName, tunnels } = group;

    return (
      <div>
        <TunnelBanner
          text={groupName}
          bgKey={groupName}
          isSelected={tunnels.includes(clickedTunnel)}
        >
          {tunnels.map((tunnel, idx) => (
            <Tunnel
              key={idx}
              tunnel={tunnel}
              isSelected={tunnel === clickedTunnel}
              onClick={onClick}
            />
          ))}
        </TunnelBanner>
      </div>
    );
  }
}

export default class extends Component {
  constructor(props) {
    super(props);
    this.state = { clickedTunnel: props.selectedTunnel };
  }

  clickTunnel(clickedTunnel) {
    this.setState({ clickedTunnel });
  }

  render() {
    const { clickedTunnel } = this.state;
    const { show, showModal, selectedTunnel, onSelectTunnel } = this.props;

    const groups = [];
    allTunnels.forEach(t => {
      let group = groups[groups.length - 1];
      if (!group || group.groupName !== t.group) {
        group = {
          groupName: t.group,
          tunnels: [],
        };

        groups.push(group);
      }

      group.tunnels.push(t);
    });

    return (
      <Modal
        className='tunnel-modal'
        show={show}
        onHide={() => showModal(false)}
        onEnter={() => this.setState({ clickedTunnel: selectedTunnel })}
        onExited={() => this.setState({ clickedTunnel: selectedTunnel })}
      >
        <Modal.Header>
          <div className='header-inner'>
            <div className='text'>
              目前選擇：<br />{selectedTunnel.group} ({selectedTunnel.name})
            </div>
            <div className='img'>
              <img src='./imgs/hole.png' alt='deco' />
            </div>
          </div>
        </Modal.Header>
        <Modal.Body
          style={{ backgroundImage: `url('./imgs/bg_modal.jpg')` }}
        >
          {groups.map((group, idx) => (
            <TunnelGroup
              key={idx}
              group={group}
              clickedTunnel={clickedTunnel}
              onClick={this.clickTunnel.bind(this)}
            />
          ))}
        </Modal.Body>
        <Modal.Footer>
          <Button
            bsStyle='primary'
            onClick={() => {
              showModal(false);
              if (clickedTunnel !== selectedTunnel) {
                onSelectTunnel(clickedTunnel);
              }
            }}
          >
            完成
          </Button>
        </Modal.Footer>
      </Modal>
    )
  }
}
